@media (min-width: 768px) {
  .filter-cls {
    display: none; }
  .filter-wrap {
    display: block; }
  .filter-mob_btn-wrap {
    display: none; } }

@media (max-width: 1399.98px) {
  .filter-bl-prc input {
    width: 80px; } }

@media (max-width: 1199.98px) {
  .filter-bl-lbl {
    font-size: 14px; }
  .filter-bl-prc input {
    width: 60px;
    font-size: 14px; }
  .filter-bl-inp input {
    font-size: 14px; }
  .filter-bl-sel select {
    font-size: 14px; } }

@media (max-width: 991.98px) {
  .filter-bl {
    width: calc(100% / 3); } }

@media (max-width: 767px) {
  .filter {
    flex-direction: column;
    margin-bottom: 0; }
    .filter-cls {
      display: block;
      position: absolute;
      z-index: 3;
      right: 15px;
      top: 15px;
      font-size: 20px; }
    .filter-wrap {
      left: 0;
      bottom: 100%;
      width: 100vw;
      height: 100vh;
      position: fixed;
      flex: unset;
      overflow: auto;
      background: #fff;
      z-index: 3;
      transition: .3s; }
      .filter-wrap.active {
        bottom: 0; }
    .filter-bl {
      width: 100%; }
      .filter-bl-lbl {
        font-size: 14px; }
      .filter-bl-prc {
        max-width: 230px; }
      .filter-bl-btn {
        margin-top: 30px;
        justify-content: flex-start; }
    .filter-mob_btn-wrap {
      display: block; } }

/*filter*/
@media (max-width: 767px) {
  .footer {
    padding: 12px 0 29px; }
  .f-soc {
    margin-top: 12px;
    margin-bottom: 35px;
    justify-content: flex-start; }
    .f-soc a {
      margin-left: 0;
      margin-right: 11px; }
      .f-soc a svg {
        width: 34px;
        height: auto; }
  .f-logo {
    margin-bottom: 19px; }
    .f-logo svg {
      width: 101px;
      height: auto; }
  .f-menu ul li {
    margin-bottom: 10px; }
    .f-menu ul li a {
      font-size: 12px; } }
